.main-header {
  display: flex;
  padding: 0;
  background: #3c8dbc;
  height: 54px;
  line-height: 1.5em;
  align-items: center;
}

.main-header .title {
  text-align: center;
  overflow: hidden;
  transition: width 0.2s;
  white-space: nowrap;
}

.main-header .title .logo {
  animation: App-logo-spin infinite 20s linear;
  width: 40px;
  height: 40px;
  vertical-align: middle;
}

.main-header .title span {
  font-size: 20px;
  margin-left: 10px;
  color: #fff;
  vertical-align: sub;
}

.main-header .bar {
  display: flex;
  flex: 1;
}

.main-header .bar ul {
  margin: 0;
  padding: 0 5px;
  display: flex;
  align-items: center;
  list-style: none;
}

.main-header .bar ul li {
  color: #fff;
  padding: 5px 15px;
  border-radius: 2px;
  transition: background-color 0.2s;
  cursor: pointer;
}

.main-header .bar ul li:hover {
  background-color: rgba(255, 255, 255, 0.2) !important;
}

.main-header .bar ul li .user-title {
  font-size: 14px;
  margin-left: 5px;
  color: #fff;
  vertical-align: middle;
}
